.filters {
	margin: 0;
	padding: 0;
	list-style: none;
	right: 0;
	left: 0;
}

.filters li {
	display: inline;
	cursor: default;
}

.filters li button {
	display: inline-block;
	color: inherit;
	margin: 3px;
	padding: 3px 7px;
	text-decoration: none;
	border: 1px solid transparent;
	border-radius: 3px;
    background-color: #fff;

}

.filters li button:not(.selected) {
	cursor: pointer;
}

.filters li:hover button {
	border-color: rgba(175, 47, 47, 0.1);
}

.filters li button.selected {
	border-color: rgba(175, 47, 47, 0.2);
}

.clear-completed,
.clear-completed:active {
	position: relative;
	line-height: 20px;
	text-decoration: none;
	cursor: pointer;
}

.clear-completed:hover {
	text-decoration: underline;
    border-color: rgba(175, 47, 47, 0.1);

}

.clear-completed{
	display: inline-block;
	color: inherit;
	margin: 3px;
	padding: 3px 7px;
	text-decoration: none;
	border: 1px solid transparent;
	border-radius: 3px;
    background-color: #fff;
    

}